import React from 'react';
import {Map, GoogleApiWrapper, Marker} from 'google-maps-react';
const mapStyles = {
  width: '80%',
  height: '50%'
};
class ShowMap extends React.Component {
  render(){
    return(
      <div>
        <Map
          google={this.props.google}
          zoom={8}
          style={mapStyles}
          initialCenter={{ lat: -33.8714234, lng: 151.2044743}}
        >
          <Marker position={{lat: this.props.latitudeFromParent, lng: this.props.longitudeFromParent }} />
        </Map>
      </div>
    );
  }
}
export default GoogleApiWrapper({
  apiKey: 'AIzaSyB2ai0xcVHX45ezIGP-E-wXg3ZKlZyzP5s'
})(ShowMap);
