import React from "react"
import axios from "axios"
import ShowMap from "./ShowMap"
const APPOINTMENT_URL = `http://localhost:3000/appointments`
const USER_URL = `http://localhost:3000/users.json`

class ShowEvent extends React.Component {
  state = {
    title: "",
    description: "",
    date: "",
    time: "",
    timeTo: "",
    aside: "",
    asideTo: "",
    lat: "",
    lng: "",
    weatherPlace: "",
    weatherTemp: "",
    weatherMain: "",
    weatherDesc: "",
  }
  componentDidMount(){
    const {match: {params: {day, month, year, appointment_id}}} = this.props;
    this.fetchAppointments();
  }
  fetchAppointments() {
    axios.get(`${APPOINTMENT_URL}/${this.props.match.params.event}.json`)
    .then(res => {
      this.setState({time: res.data.time.substr(11,8), title: res.data.title, description: res.data.description, date: res.data.date, timeTo: res.data.timeTo.substr(11,8), aside: res.data.aside, asideTo: res.data.asideTo, lat: res.data.latitude, lng: res.data.longitude});
      this.getWeather()
    })
    .catch(error => console.warn(error));
  }
  getWeather = () => {
    axios.get(`http://api.openweathermap.org/data/2.5/weather?lat=${this.state.lat}&lon=${this.state.lng}&APPID=44c4ec8bdab3135de4699fa100275470`)
    .then(res => {
      this.setState({weatherPlace: res.data.name, weatherTemp: Math.round(res.data.main.temp-273.15), weatherDesc: res.data.weather[0].description, weatherMain: res.data.weather[0].main})
    })
    .catch(console.warn())
  }
  render(){
    return(
      <div>
        <div>Title: {this.state.title}</div>
        <div>Description: {this.state.description}</div>
        <div>Time: {this.state.time} <span> {this.state.aside}</span></div>
        <div>Time To: {this.state.timeTo} <span> {this.state.asideTo}</span></div>
        <div>Date: {this.state.date}</div>
        <div>Weather in {this.state.weatherPlace}: {this.state.weatherTemp}°C with {this.state.weatherMain}</div>
        <ShowMap longitudeFromParent = {this.state.lng} latitudeFromParent={this.state.lat}/>
      </div>
    )
  }
}

export default ShowEvent
