import React from "react"
import "../css/style.css"
import $ from "jquery"
import Login from "./Login"
import Signup from "./Signup"
import {
  Route,
  Link,
  HashRouter as Router
} from "react-router-dom"
import axios from "axios"
import ShowDate from "./ShowDate"
import ShowEvent from "./ShowEvent"
import Calendar from 'react-calendar/dist/entry.nostyle';
import PropTypes from 'prop-types'
class ShowCalendar extends React.Component {
  state = {
    date: new Date(),
    appointmentsReceived: [],
    loginPressed: false,
    signupPressed: false
  }
  onChange = date => this.setState({date})

  clickDay = (dateInfo) => {
    const data = dateInfo.toString().split(" ");
    const day = data[2];
    let month = data[1];
    const year = data[3];
    const monthNum = ["01","02","03","04","05","06","07","08","09","10","11","12"]
    const monthShort = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]
    month = monthNum[monthShort.indexOf(month)]
    const route = `/calendar/${day}/${month}/${year}`;
    this.props.history.push(route);
  }
  ShowLogin = () => {
    this.setState({loginPressed: true, signupPressed: false})
  }
  closeLogin = () => {
    this.setState({loginPressed: false, signupPressed: false})
  }
  ShowSignup = () => {
    this.setState({signupPressed: true, loginPressed: false})
  }
  closeSignup = () => {
    this.setState({signupPressed: false, loginPressed: false})
  }
  render() {
    return(
      <div>
        {this.state.loginPressed === false && <div onClick={this.ShowLogin}>Login</div>}
        {this.state.loginPressed === true && <div onClick={this.closeLogin}>X</div>}
        {this.state.loginPressed === true && <div><Login/></div>}
        {this.state.loginPressed === false && this.state.signupPressed === false && <Calendar onChange={this.onChange} value = {this.state.state} onClickDay = {this.clickDay} />}
        {this.state.signupPressed === true && <div onClick={this.closeSignup}>X</div>}
        <span onClick={this.ShowSignup}>
          {this.state.signupPressed === false && <div onClick={this.Signup}>Signup</div>}
          {this.state.signupPressed === true && this.state.loginPressed === false && <div><Signup/></div>}
        </span>
      </div>
    )
  }
}

export default ShowCalendar;
